main {
    padding: 20px 0;  
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
        @media (min-width: 769px){
            align-items: flex-start
        }
}

div p {
    margin: 0 2rem;
}

main section h2 {
    letter-spacing: 1px;
    text-align: center;  
    margin-bottom: 50px ;
    font-family: 'Oswald', sans-serif;
    font-size: 1.75em;
    color: rgba(0,0,0,1);
}

