.projects {
    max-width: 100%;
    display: flex;
    flex-direction: column;
    text-align: center;
    margin: 60px 0;
    @media (max-width: 1000px){
        text-align: center;
        & h2,.cardContainer{
            margin-left:10px;
            margin-right: 10px;
        }
    }
}

.container {
    text-align: center;
}

.cards {
    display: flex;
    flex-wrap: wrap;
    text-align: center;
    margin: 0 2.5rem;
    margin-left: 4rem;
}

.card {
    background-size: cover;
    width: 26vw;
    height: 100%;
    margin: 0 auto;
    margin-bottom: 2rem;
    background-color: #fff;
    // border: 4px solid transparent;
    border-radius: 11px 11px 11px 11px;
    -webkit-box-shadow: 0px 0px 14px -4px #000000; 
    box-shadow: 0px 0px 14px -4px #000000;
    color: rgba(0,0,0,1);
}

a:link {
    text-decoration: none;
}

.card img {
    width: 100%;
}

.card-title {
    margin: 1.5rem;
    font-size: 1.5rem;
}

.btn-more {
    text-align: right;
    margin: 1.5rem;
    color: red;
}